@import url("general.css");
/***********************************
HEADER SECTION
***********************************/
.section--header {
  font-size: 5.6rem;
  margin-bottom: 9.6rem;
  letter-spacing: -1px;
}

.section--subheader {
  display: block;
  color: var(--main-color);
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 1.6rem;
  text-transform: uppercase;
}

.header-section {
  display: flex;
  flex-direction: column;
  row-gap: 16rem;
  padding: 1.2rem 0;
}

.header-nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-size: 2rem;
}

.nav-list {
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style: none;
  font-weight: 500;
  min-width: 43%;
  margin-right: 2.4rem;
}

.nav-logo {
  color: #1b1b1b;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 2.6rem;
  display: flex;
  gap: 0.8rem;
  align-items: center;
  padding-left: 2.4rem;
}

.logo-img {
  width: 50%;
}

.list-item a {
  font-size: 1.6rem;
}

.list-item--link {
  position: relative;
}

.list-item:last-of-type a::before {
  content: none;
}
.list-item--link:link,
.list-item--link:visited {
  text-decoration: none;
  color: #1b1b1b;
  font-weight: 700;
  display: inline-block;
  padding: 1.2rem 2.4rem;
  border-radius: 10px;
  transition: all 300ms;
}

.list-item--link:hover,
.list-item--link:active {
  text-decoration: none;
  color: var(--main-color);

  display: inline-block;
}
.list-item--link.cta-link:link,
.list-item--link.cta-link:visited {
  color: #fff;
  background-color: var(--main-color);
}

.list-item--link.cta-link:hover,
.list-item--link.cta-link:active {
  background-color: var(--dark-main-color);
}
/***********************************
  HERO SECTION
***********************************/
.section-hero {
  /* padding: 9.6rem 0; */

  max-width: 130rem;
  margin: 0 auto;
  padding: 4.8rem 0 9.6rem 0;

  align-items: center;
  justify-content: space-around;
}

.hero-box {
  padding: 0 3.2rem;
  column-gap: 9.6rem;
}

.hero-text {
  place-self: center center;
}

.hero-text p {
  font-size: 2rem;
  line-height: 1.6;

  margin-bottom: 5.6rem;
}

.hero-buttons {
  display: flex;
  column-gap: 4.2rem;
}

.hero-img-box {
  width: 90%;
  align-self: flex-start;
  justify-self: center;
}

.hero-img {
  width: 100%;
  display: block;
}
/***********************************
  HOW IT WORKS SECTION
***********************************/

.section-how {
  max-width: 120rem;
  margin: 0 auto;
  column-gap: 9.6rem;
  padding: 9.6rem 0 0 0;
  /* background-color: #f1f1f1; */
}

.section-how-location {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 9.6rem;
  margin-bottom: 9.6rem;
}

.section-how-car {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 9.6rem;
  margin-bottom: 9.6rem;
}
.section-how-car .section-text--box {
  grid-column: 2;
}

.section-how-car .section-img--box {
  grid-column: 1;
  grid-row: 1;
}

.section-how-payment {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 9.6rem;
  margin-bottom: 9.6rem;
}

.step-number {
  display: block;
  font-size: 8.6rem;
  color: var(--main-color);
  font-weight: 900;
  letter-spacing: -4px;
  margin-bottom: 1.8rem;
}

.section-text--box h2 {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.section-text--box p {
  font-size: 1.8rem;
  line-height: 2;
}

.section-img--box {
  text-align: center;
}

.section-img {
  width: 40%;
}

/***********************************
  CAR CATEGORIES SECTION
***********************************/
.section-car-categories {
  padding: 9.6rem 0;
}

.section-car-categories .section--header,
.section-car-categories .section--subheader {
  text-align: center;
}

.car-categories-box {
  column-gap: 6.8rem;
  text-align: center;
}

.car-category-box {
  padding: 5.8rem 2.4rem;
  border-radius: 10px;
  background-color: #f1f1f1;
  box-shadow: 0px 10px 12px 1px rgb(0 0 0 / 20%);
  transition: 300ms all;
}
.car-category-box:hover {
  transform: translateY(-1.5rem);
  /* background-color: #eee; */
}

.car-category--header {
  font-size: 3.2rem;
  font-weight: 600;

  letter-spacing: 1px;
  display: block;
  margin-bottom: 1.4rem;
}
.car-category--subheader {
  color: #1b1b1b;
  text-transform: uppercase;
  font-size: 1.4rem;
  display: inline-block;
  margin-bottom: 4.8rem;
}
.car-category-box i.car-list--icon {
  margin-right: 1.2rem;
  font-size: 1.6rem;
}

.special-category-box {
  background-color: var(--light-main-color);
  color: #1b1b1b;
}

.special-category-box i.car-list--icon {
  color: #1b1b1b #;
}

.car-category--lists {
  gap: 3.2rem;
  align-items: center;
  justify-content: center;
}
.car-category--list {
  list-style: none;

  text-align: left;
  /* padding: 0 0 0 2.4rem; */
  gap: 2.4rem;
}

.car-list--item,
.car-list--item span {
  display: flex;
  font-size: 2rem;
  align-items: center;
}

.car-list--item:last-of-type {
  margin-bottom: 0;
}

.car-list--icon {
  color: #1b1b1b;
  height: 1.8rem;
  width: 1.8rem;
  margin-right: 1rem;
}

/***********************************
  SELECT A CAR SECTION
***********************************/
.section-used-cars {
  padding: 9.6rem 0;
}

.used-cars-box {
  width: 90%;
  max-height: 50rem;
  overflow-y: scroll;
  border-radius: 10px;
  margin: 0 auto;
}
::-webkit-scrollbar {
  width: 1.2rem;
}
::-webkit-scrollbar-track {
  background: #eee;
  border-radius: 10px;
  margin-block: 0.5rem;
}
::-webkit-scrollbar-thumb {
  background: #1b1b1b;
  border: 0.1rem solid #ddd;
  border-radius: 10px;
}

.used-car-box {
  /* padding: 1.8rem; */
  grid-template-columns: 40% 43% 17%;
}

.used-cars-inner {
  padding: 3.6rem;
  gap: 2.4rem;
}

.used-cars-item {
  border: 1px solid #ddd;
  border-radius: 10px;
  overflow: hidden;
}
.used-car-img {
  width: 100%;
  border-radius: 10px 0 0 10px;
  transition: 400ms all;
  display: block;
  align-self: center;
  justify-self: center;
}
.used-car-img:hover {
  transform: scale(1.1);
}

.used-car-description {
  padding: 1.8rem 2.4rem;
  gap: 2.4rem;
}

.car-description-header {
  font-size: 2.4rem;
  /* width: 70%; */
  text-align: center;
}

.car-desciption-items {
  width: 70%;
  font-size: 1.2rem;
  color: #777;
  flex-wrap: wrap;
  align-self: center;
  justify-content: space-between;
  row-gap: 2.4rem;
  column-gap: 6.8rem;
}

.car-description-item {
  flex: 1 0 34%;
  /* width: 35%; */
  padding: 2.4rem 0 0 0;
  gap: 1rem;
  align-items: flex-start;
}
.car-desciption-item:nth-of-type(even) {
  align-self: flex-end;
}
.car-description-item span {
  display: block;
}

.description-sub-item {
  color: #444;
  display: block;
}

.car-description-more {
  font-size: 1.4rem;
  color: #1b1b1b;
  text-align: center;
}

.buy-used-car {
  align-items: center;
  justify-content: center;
  gap: 4.8rem;
  box-shadow: -0.5px 0px 0px 0px #ddd;
}

.buy-used-car h3 {
  font-size: 2.4rem;
  font-weight: 400;
}

.buy-used-car a:link,
.buy-used-car a:visited {
  font-size: 1.8rem;
  text-decoration: none;
  color: #fff;
  background-color: #1b1b1b;
  padding: 1.2rem 2.4rem;
  border-radius: 10px;
  transition: 300ms all;
}

.buy-used-car a:hover,
.buy-used-car a:active {
  color: var(--light-main-color);
}

/***********************************
  LONG TERM RENT SECTION
***********************************/

.section-longterm-cta {
  padding: 9.6rem 0;
}

.cta-form-box {
  background-color: var(--light-main-color);
  padding: 0 0 0 2.4rem;
  border-radius: 10px;
  grid-template-columns: 70% 30%;
  overflow: hidden;
}
.cta-text-box {
  padding: 4.8rem 4.8rem 3.6rem 3.6rem;
}
.cta-header {
  font-size: 4.2rem;
  margin-bottom: 3.2rem;
}

.cta-text {
  font-size: 2rem;
  line-height: 1.4;
  margin-bottom: 3.8rem;
}
.cta-form {
  row-gap: 2.8rem;
  column-gap: 3.6rem;
  grid-template-columns: 1fr 1fr;
}

.cta-form label {
  display: block;
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.cta-form input {
  width: 100%;
  font-size: 2.4rem;
  padding: 1.2rem;
  border: none;
  border-radius: 10px;
}

.cta-form select {
  width: 100%;
  border: none;
  font-size: 2.4rem;
  font-weight: 700;
  padding: 1.2rem;

  border-radius: 10px;
}

.btn--form {
  width: 100%;
  display: block;
  font-size: 2.4rem;
  font-weight: 700;
  padding: 1.2rem;
  border-radius: 10px;
  background-color: #1b1b1b;
  border: 0px solid;
  color: #fff;
  align-self: flex-end;
  transition: 300ms all;
  cursor: pointer;
}

.btn--form:hover {
  background-color: #fff;
  /* border-color: #fff; */
  color: #1b1b1b;
}

img.cta-img-box {
  display: block;
  width: 100%;
}

/***********************************
  FOOTER SECTION
***********************************/

.section-footer {
  padding: 9.6rem 0;
  border-top: 1px solid #eee;
  font-size: 1.6rem;
}
.grid-footer {
  grid-template-columns: 1.7fr 1.7fr 1fr 1fr 1fr;
  column-gap: 3.2rem;
}

.grid-footer > div {
  /* border: 1px solid red; */
  padding: 0 1.2rem;
}

.logo-box img {
  height: 2.2rem;
}

.footer-logo {
  display: block;
  margin-bottom: 3.2rem;
}

.logo {
  height: 2.2rem;
}

.social-networks {
  list-style: none;
  gap: 2.4rem;
  margin-bottom: 3.2rem;
}

.social-networks i {
  color: #444;
  font-size: 2rem;
}

.imported-icons {
  margin-bottom: 2.4rem;
}
.imported-icons,
.copyright {
  font-size: 1rem;
}

.copyright {
  margin-bottom: 1rem;
}

.imported-icons a {
  text-decoration: none;
  font-weight: 500;
  color: inherit;
}

.nav-box,
.address-box {
  gap: 2.6rem;
}
.address-box p {
  line-height: 1.2;
}
address {
  font-style: normal;
}
.company-address a {
  text-decoration: none;
  color: inherit;
}

.footer-subheader {
  font-size: 2rem;
  font-weight: 500;
  text-align: left;
}
.footer-nav,
.company-address {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  list-style: none;
  align-items: left;
}

.footer-nav a {
  text-decoration: none;
  color: #444;
}
